#pardot-form {
  .pd-select {
    @include form-v2-select;

    select {
      border: var(--form-field-input-border-width, 1px) solid var(--form-field-input-border-color, $color-dark-grey);
      border-radius: var(--form-field-input-border-radius, #{getBorderRadius(0)});
      background-color: var(--form-field-input-background-color, $color-white);
      color: var(--form-field-input-text-color);
      @include gt-body-text('small');
      width: 100%;
    }

    &.filled,
    &.focus {
      select {
        color: var(--form-field-input-text-color, $color-black);
      }
    }
  }
}